.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 40px;
}

.input-container {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
}

.input-container input {
    border-style: none;
    border: 1px solid rgb(62, 62, 62);
    border-radius: 5px;
    height: 25px;
    width: 200px;
}

.input-container button {
    border-style: none;
    border: 1px solid rgb(62, 62, 62);
    border-radius: 5px;
    height: 25px;
    width: 100px;
}

.qr-container {
    /* border: 2px solid rgb(53, 53, 53); */
    padding: 80px;
    border-radius: 10%;
    background: rgb(0, 153, 255);
    display: flex;
    align-items: center;
    justify-content: center;

}

